import React from "react";
import {
  FaLinkedin,
  FaFacebookF,
  FaFileDownload,
  FaRegAddressCard,
  FaGithub
} from "react-icons/fa";
import {
  HomePageContainer,
  BackgroundContainer,
  InfoContainer,
  StyledTypist,
  DescriptionContainer,
  AvatarContainer
} from "./home-page.styles";
import TypingText from "../../components/typing-text/typing-text.component";

const HomePage = props => {
  return (
    <HomePageContainer>
      <AvatarContainer>
        <img src="/avatar.jpg" alt="avatar" />
      </AvatarContainer>
      <InfoContainer>
        <StyledTypist>
          <h1>TRẦN NGUYÊN</h1> <p>Front-end Developer</p>
        </StyledTypist>
        <h3>
          <a href="facebook.com">
            <FaGithub />
          </a>
          <a href="facebook.com">
            <FaFacebookF />
          </a>
          <a href="https://www.linkedin.com/in/nguyen-tran-1231b0180/">
            <FaLinkedin />
          </a>
        </h3>
      </InfoContainer>
      <TypingText infinite="8">It will never be perfect.</TypingText>
      <TypingText delay="5000ms">Stop thinking. Just do more!</TypingText>

      <DescriptionContainer>
        <a href="https://google.com">
          <div>DOWNLOAD CV</div>
          <h3>
            <FaFileDownload />
          </h3>
        </a>
        <a href="https://google.com">
          <div>CONTACT ME</div>
          <h3>
            <FaRegAddressCard />
          </h3>
        </a>
      </DescriptionContainer>
      <BackgroundContainer />
    </HomePageContainer>
  );
};

export default HomePage;
